	module.exports = {"data":{"post":{"html":"<p>Sorry I've so quiet here lately, it seems I've lost my voice. We've both been on the floor running shifts at work (one shift leader sick, another on holiday) and we've been doing what McDonald's insiders affectionately refer to as \"running labor\", i.e. running slightly understaffed to make up for previous shifts that were overstaffed in order to meet a goal for labor costs. It's kind of like sticking your feet in the oven when you're head is cold so that your average temperature is OK.</p>\n<p>Actually, I think the expression is \"sticking your head in the oven when your feet are cold\", but that made me think of <a href=\"http://en.wikipedia.org/wiki/Sylvia_Plath#Death\">Sylvia Plath</a>, and such a dire impression is not the one what I wanted to make.</p>\n<p>Whatever creative outbursts I've had have easily fit into the 140 character limit at Twitter. I'll try harder to be more creative in the future.</p>","fields":{"title":"Laryngitis","date":"25 Feb 2008","url":"https://papascott.de/2008-02-25-laryngitis/"}}},"pathContext":{"slug":"/2008-02-25-laryngitis/","prev":{"fields":{"title":"Pinko Commies","date":"17 Feb 2008","slug":"/2008-02-17-pinko-commies/"}},"next":{"fields":{"title":"We Need The Eggs","date":"01 Mar 2008","slug":"/2008-03-01-we-need-the-eggs/"}}}}

